import { BEComponent } from './BEComponent';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { BEMethod } from './BEMethod';
import { ReturnValue } from '../BaseEntity/ReturnValue';
import { stringify } from '@angular/core/src/util';

export class BEValidation {
    static ValidateBeforeSave(component: BEComponent): boolean {
        if (isNullOrUndefined(component)) {
            alert('null');
            return false;
        }
        if (!this.ValidateBasicInfo(component)) {
            return false;
        }
        if (!this.ValidateParameterInfo(component.BEMethod)) {
            return false;
        }
        if (!this.ValidateReturnValueInfo(component.BEMethod.ReturnValue)) {
            return false;
        }
        return true;
    }
    static ValidateBasicInfo(component: BEComponent): boolean {
        if (isNullOrEmptyString(component.ComponentCode)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.ComponentName)) {
            alert('');
            return false;
        }
        if (isNullOrUndefined(component.BEMethod)) {
            alert('null');
            return false;
        }
        if (isNullOrEmptyString(component.BEMethod.Assembly)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.BEMethod.ClassName)) {
            alert('');
            return false;
        }
        return true;
    }
    static ValidateParameterInfo(method: BEMethod): boolean {
        if (method.BEParams.length === 0) {
            return true;
        }
        let isErrorFlag = false;
        for (let rowIndex = 0; rowIndex < method.BEParams.length; rowIndex++) {
            const parameter = method.BEParams[rowIndex];
            if (isNullOrEmptyString(parameter.ParamCode)) {
                alert('' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            if (isNullOrEmptyString(parameter.ParamName)) {
                alert('' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            if (isNullOrEmptyString(parameter.Assembly)) {
                alert('' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            if (isNullOrEmptyString(parameter.ClassName)) {
                alert('' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            const nextRowIndex = rowIndex + 1;
            for (let afterRowIndex = nextRowIndex; afterRowIndex < method.BEParams.length; afterRowIndex++) {
                const afterParameter = method.BEParams[afterRowIndex];
                if (parameter.ParamCode === afterParameter.ParamCode) {
                    alert('' + (rowIndex + 1) + '' + (afterRowIndex + 1) + '');
                    isErrorFlag = true;
                    break;
                }
                if (parameter.ParamName === afterParameter.ParamName) {
                    alert('' + (rowIndex + 1) + '' + (afterRowIndex + 1) + '');
                    isErrorFlag = true;
                    break;
                }
            }
        }
        if (isErrorFlag) {
            return false;
        } else {
            return true;
        }
    }
    static ValidateReturnValueInfo(returnValue: ReturnValue): boolean {
        if (returnValue.ClassName === 'Void' && returnValue.Assembly === 'mscorlib.dll') {
            return true;
        }
        if (isNullOrEmptyString(returnValue.Assembly)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(returnValue.ClassName)) {
            alert('');
            return false;
        }
        return true;
    }
}



