import { UDTValidComponent } from './UDTValidComponent';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';

export class UDTValidValidation {
    static ValidateBeforeSave(component: UDTValidComponent): boolean {
        if (isNullOrUndefined(component)) {
            alert('null');
            return false;
        }
        if (!this.ValidateBasicInfo(component)) {
            return false;
        }
        return true;
    }
    static ValidateBasicInfo(component: UDTValidComponent): boolean {
        if (isNullOrEmptyString(component.ComponentCode)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.ComponentName)) {
            alert('');
            return false;
        }
        if (isNullOrUndefined(component.UDTValidMethod)) {
            alert('null');
            return false;
        }
        if (isNullOrEmptyString(component.UDTValidMethod.Assembly)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.UDTValidMethod.ClassName)) {
            alert('');
            return false;
        }
        return true;
    }
}



