import { UDTDtmComponent } from './UDTDtmComponent';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';

export class UDTDtmValidation {
    static ValidateBeforeSave(component: UDTDtmComponent): boolean {
        if (isNullOrUndefined(component)) {
            alert('null');
            return false;
        }
        if (!this.ValidateBasicInfo(component)) {
            return false;
        }
        return true;
    }
    static ValidateBasicInfo(component: UDTDtmComponent): boolean {
        if (isNullOrEmptyString(component.ComponentCode)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.ComponentName)) {
            alert('');
            return false;
        }
        if (isNullOrUndefined(component.UDTDtmMethod)) {
            alert('null');
            return false;
        }
        if (isNullOrEmptyString(component.UDTDtmMethod.Assembly)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.UDTDtmMethod.ClassName)) {
            alert('');
            return false;
        }
        return true;
    }
}



