import { DeterminationComponent } from './DeterminationComponent';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';

export class DtmValidation {
    static ValidateBeforeSave(component: DeterminationComponent): boolean {
        if (isNullOrUndefined(component)) {
            alert('null');
            return false;
        }
        if (!this.ValidateBasicInfo(component)) {
            return false;
        }
        return true;
    }
    static ValidateBasicInfo(component: DeterminationComponent): boolean {
        if (isNullOrEmptyString(component.ComponentCode)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.ComponentName)) {
            alert('');
            return false;
        }
        if (isNullOrUndefined(component.DeterminationMethod)) {
            alert('null');
            return false;
        }
        if (isNullOrEmptyString(component.DeterminationMethod.Assembly)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.DeterminationMethod.ClassName)) {
            alert('');
            return false;
        }
        return true;
    }
}



