import { ValidationComponent } from './ValidationComponent';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';

export class VldValidation {
    static ValidateBeforeSave(component: ValidationComponent): boolean {
        if (isNullOrUndefined(component)) {
            alert('null');
            return false;
        }
        if (!this.ValidateBasicInfo(component)) {
            return false;
        }
        return true;
    }
    static ValidateBasicInfo(component: ValidationComponent): boolean {
        if (isNullOrEmptyString(component.ComponentCode)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.ComponentName)) {
            alert('');
            return false;
        }
        if (isNullOrUndefined(component.ValidationMethod)) {
            alert('null');
            return false;
        }
        if (isNullOrEmptyString(component.ValidationMethod.Assembly)) {
            alert('');
            return false;
        }
        if (isNullOrEmptyString(component.ValidationMethod.ClassName)) {
            alert('');
            return false;
        }
        return true;
    }
}



