import { VMComponent } from './VMComponent';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { VMMethod } from './VMMethod';
import { ReturnValue } from '../BaseEntity/ReturnValue';

export class VMValidation {
    static ValidateBeforeSave(component: VMComponent): boolean {
        if (isNullOrUndefined(component)) {
            alert('VMnull');
            return false;
        }
        if (!this.ValidateBasicInfo(component)) {
            return false;
        }
        if (!this.ValidateParameterInfo(component.VMMethod)) {
            return false;
        }
        if (!this.ValidateReturnValueInfo(component.VMMethod.ReturnValue)) {
            return false;
        }
        return true;
    }
    static ValidateBasicInfo(component: VMComponent): boolean {
        if (isNullOrEmptyString(component.ComponentCode)) {
            alert('VM');
            return false;
        }
        if (isNullOrEmptyString(component.ComponentName)) {
            alert('VM');
            return false;
        }
        if (isNullOrUndefined(component.VMMethod)) {
            alert('VMnull');
            return false;
        }
        if (isNullOrEmptyString(component.VMMethod.Assembly)) {
            alert('VM');
            return false;
        }
        if (isNullOrEmptyString(component.VMMethod.ClassName)) {
            alert('VM');
            return false;
        }
        return true;
    }
    static ValidateParameterInfo(method: VMMethod): boolean {
        if (method.VMParams.length === 0) {
            return true;
        }
        let isErrorFlag = false;
        for (let rowIndex = 0; rowIndex < method.VMParams.length; rowIndex++) {
            const parameter = method.VMParams[rowIndex];
            if (isNullOrEmptyString(parameter.ParamCode)) {
                alert('VM' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            if (isNullOrEmptyString(parameter.ParamName)) {
                alert('VM' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            if (isNullOrEmptyString(parameter.Assembly)) {
                alert('VM' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            if (isNullOrEmptyString(parameter.ClassName)) {
                alert('VM' + (rowIndex + 1) + '');
                isErrorFlag = true;
                break;
            }
            const nextRowIndex = rowIndex + 1;
            for (let afterRowIndex = nextRowIndex; afterRowIndex < method.VMParams.length; afterRowIndex++) {
                const afterParameter = method.VMParams[afterRowIndex];
                if (parameter.ParamCode === afterParameter.ParamCode) {
                    alert('VM' + (rowIndex + 1) + '' + (afterRowIndex + 1) + '');
                    isErrorFlag = true;
                    break;
                }
                if (parameter.ParamName === afterParameter.ParamName) {
                    alert('VM' + (rowIndex + 1) + '' + (afterRowIndex + 1) + '');
                    isErrorFlag = true;
                    break;
                }
            }
        }
        if (isErrorFlag) {
            return false;
        } else {
            return true;
        }
    }
    static ValidateReturnValueInfo(returnValue: ReturnValue): boolean {
        if (returnValue.ClassName === 'Void' && returnValue.Assembly === 'mscorlib.dll') {
            return true;
        }
        if (isNullOrEmptyString(returnValue.Assembly)) {
            alert('VM');
            return false;
        }
        if (isNullOrEmptyString(returnValue.ClassName)) {
            alert('VM');
            return false;
        }
        return true;
    }
}



